import React, { useEffect } from "react";
import "./Content.css";
import Aos from "aos";
import "aos/dist/aos.css";

export default function Content() {
  useEffect(() => {
    Aos.init({});
  }, []);
  return (
    <div className="outsideContainer">
      <div data-aos="flip-left" className="boxes box_1">
        <div className="Des">
          <h2 className="text-center">DESCRIPTION</h2>
          <p className="content">
            There's one simple reason we call this our everyday oxford, we wear
            them preety much every single day.
          </p>
        </div>
      </div>
      <div data-aos="flip-left" className="boxes box_2">
        <div className="mater">
          <h2 className="text-center">MATERIAL</h2>
          <p className="content">
            Just the right amount of sturdy for year round wear. Soft to the
            touch but built for a decade of solid wear. Oxford ios a perfect
            versatile fabric that looks just as good pressed as it does pulled
            of the back of a chair. While traditional cotton uses 25 percent of
            the world's pesticides, our organic cotton doesn't use a drop. A
            better shirt, responsibly built for the long haul.
          </p>
        </div>
      </div>
      <div data-aos="flip-left" className="boxes box_3">
        <div className="specs">
            <h2 className="text-center">SPECIFICATIONS</h2>
            <ul>
                <li>100% organic cotton</li>
                <li>Made from our burly rughy oxford</li>
                <li>Our signature Jack button down collar</li>
                <li>Single rounded chest pocket</li>
                <li>Double-needle felled construction thoughout</li>
                <li>Natural buttons</li>
                <li>Lock-stiched buttons and buttonholes</li>
                <li>Shorter length to be worn untucked</li>
                <li>Tailored fit and high armhole</li>
                <li>No pleats for easy ironing and clean lines</li>
                <li>Wash cold and tumble dry low</li>
                <li>Made in China</li>
                
            </ul>

        </div>
      </div>
      <div data-aos="flip-left" className="boxes box_4">
        <div className="essent">
          <h2 className="text-center">Essential</h2>
          <p className="content">
            This product is a Taylor Stitch Essential that we aim to always keep in stock. Essential are our tried and true product that we wear damn near everyday. If your size is currently out-of-stock, please submit your email address to the <strong>'Notify me'</strong> tab. We restock Essentials regularly. In stock sizes are availabe for Immediate shipping
          </p>
        </div>
      </div>
    </div>
  );
}
