import React, { useEffect, useState } from "react";
import Aos from "aos";
import "aos/dist/aos.css";
import "./Submitform.css";
import { click } from "@testing-library/user-event/dist/click";

export default function Submitform(props) {
  useEffect(() => {
    Aos.init({});
  }, []);

  const [text, setText] = useState('');

  const clickMe = (event) =>{
      setText(event.target.value)

  }
  const clickMe2 =() => {
    alert('thanks for your response we will get back to you ass soon as possible!!')
  }
  

  return (
    <div data-aos="flip-left" className="Submit_form">
      <form>
        <div className="mb-3">
          <label for="exampleInputEmail1" className="form-label">
            Email address
          </label>
          <input
            onChange={clickMe}
            value={text}
            type="email"
            className="form-control"
            id="exampleInputEmail1"
            aria-describedby="emailHelp"
          />
          <div id="emailHelp" className="form-text">
            We'll never share your email with anyone else.
          </div>
        </div>
        <button disabled={text.length<=0} onClick={clickMe2} type="submit" className="btn btn-primary">
          NOTIFY ME
        </button>
      </form>
    </div>
  );
}
